import { Component, OnInit, ÉµConsole } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { UtilService } from 'src/app/services/util.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { PaymentTeamService } from 'src/app/services/payment-team.service';
import { ListsHomeService } from 'src/app/services/lists-home.service';
import { Platform } from '@ionic/angular';
import { Location } from '@angular/common';

@Component({
  selector: 'app-results',
  templateUrl: './results.page.html',
  styleUrls: ['./results.page.scss'],
})
export class ResultsPage implements OnInit {

  public modelGame  : Model;
  //public list : any = [];
  public tabs = []
  public tabSelected :any;
  public lastFocusSegment : any;
  public listSkeleton = new Array(3)
  public firstLoad = true;

  constructor(
    public request : RequestService,
    public util : UtilService,
    public dialogService : DialogService,
    public paymentTeamService : PaymentTeamService,
    public listsHomeService : ListsHomeService,
    public platform : Platform,
    private location: Location
  ) {
    this.modelGame = new Model('Game',request);

     this.platform.backButton.subscribeWithPriority(10, (processNextHandler) => {
    //   alert('Handler was called!');
      
       if(this.platform.is('android'))
          this.location.back();

        //alert(this.location.path);

        if(this.location.isCurrentPathEqualTo('/home/results')){
          //alert('is list results')
          this.closeApp();
        }

        
    //     alert('is android');    
     });

   }

  ngOnInit() {
    this.init();
  }

  init(event = null){
    /*this.modelGame.api_function('results').subscribe(data => {
      if(data['status'] = 'success'){
        this.list = data['data'];
        if(event)
          event.target.complete();
      }
    })
*/
    //await this.delay(5000);
    this.modelGame.api_function('pageHomeResults').subscribe(
      response => {
        if(response['status'] == 'success'){
          this.tabs = response['data']['dates'];
          this.selectLast();
        }
        if(event)
          event.target.complete()
        console.log(response)
        this.firstLoad = false;
      },
      error => {
        if(event)
          event.target.complete()
        console.error(error)
        this.firstLoad = false;
      }
    )
  }

   async selectLast(){
    if(this.tabs.length > 0){
      let tab = this.tabs.slice(-1)[0];
      this.listsHomeService.results = tab['results']
      this.tabSelected = this.tabs.length - 1
      await this.delay( 1500 )
      this.focusSegment()
      //this.selectDate(tab['date']);
    }
  }

  async selectDate(event){
    let index = parseInt( event.detail.value );

    let tab = this.tabs[index];

    if(tab['results']){
      this.listsHomeService.results = tab['results']
      this.focusSegment()
      return;
    }
    
    await this.dialogService.loadingProgres();
    
    this.modelGame.api_function('resultsByDate',{'date' : tab['date']}).subscribe(
      response => {
        if(response['status'] == 'success'){
          this.tabs[index]['results'] = response['data']
          this.listsHomeService.results = this.tabs[index]['results'];
          this.focusSegment()
        }

        this.dialogService.dimissLoaging();
        console.log(response);
      },
      error => {
        console.error(error);
        this.dialogService.dimissLoaging();

      }
    )

  }

  focusSegment() {
    let segmentId = 'sgm_r_' + this.tabSelected;
    console.log('select segment : ', segmentId)
    document.getElementById(segmentId).scrollIntoView({ 
      behavior: 'smooth',
      block: 'center',
      inline: 'center'
    });
  }

  
  delay(ms: number) {
    return new Promise( resolve => setTimeout(resolve, ms) );
  }

  formatDate(val){
    return this.util.setFormatMoment(val,'DD MMMM YYYY')
  }

  closeApp(){
    //this.platform.exitApp();
    navigator['app'].exitApp();
  }

}
